@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes bounce {
  0%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(-12px);
            transform: translateY(-12px); } }
@keyframes bounce {
  0%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(-12px);
            transform: translateY(-12px); } }

html {
  scroll-behavior: smooth; }

.bounce {
  -webkit-animation-name: bounce;
          animation-name: bounce;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

.App {
  text-align: center;
  background: -webkit-linear-gradient(top left, #8adbcd, #8adbcd 5%, #65ccbb, #65ccbb 25%, #57ba98 25%, #57ba98 85%, #65ccbb 85%, #65ccbb, #8adbcd 5%, #8adbcd);
  background: linear-gradient(to bottom right, #8adbcd, #8adbcd 5%, #65ccbb, #65ccbb 25%, #57ba98 25%, #57ba98 85%, #65ccbb 85%, #65ccbb, #8adbcd 5%, #8adbcd);
  min-height: 500px;
  -webkit-transition: all ease 0.1s;
  transition: all ease 0.1s;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.App-logo {
  height: 20vmin;
  margin-bottom: 15px;
  border-radius: 20%;
  border-style: solid;
  border-color: #e4e3e3;
  border-width: 3px; }

.App-header {
  color: white; }
  .App-header__icon {
    top: 5px; }

.App-link {
  color: #61dafb; }

.btn {
  color: white;
  text-decoration: none;
  display: block;
  font-family: sans-serif;
  margin-bottom: 10px;
  padding: 7px; }
  .btn:hover {
    background-color: #5688c2; }

.contact {
  background-color: gray; }

div.content {
  width: 100%;
  padding-top: 20px; }
  div.content h1 {
    margin-top: 0; }

.workplace {
  margin: auto; }

a.jump-links {
  text-decoration: none;
  color: black; }

.punc {
  color: #3b945e; }

#home-content {
  color: white;
  position: relative;
  background: #dddddd;
  padding: 40px;
  min-height: 50vh;
  color: #5c5c5c; }

#projects-content {
  color: white;
  position: relative;
  background: -webkit-linear-gradient(top right, #f2ebda, #f2ebda 22%, #f7f4ec 22%, #f7f4ec 70%, #f2ebda 70%, #f2ebda);
  background: linear-gradient(to bottom left, #f2ebda, #f2ebda 22%, #f7f4ec 22%, #f7f4ec 70%, #f2ebda 70%, #f2ebda);
  padding: 25px;
  min-height: 50vh;
  box-sizing: border-box; }
  #projects-content .project-details {
    background-color: #f7f4ec;
    padding: 10px; }

#hobbies-content {
  color: white;
  position: relative;
  padding-bottom: 40px;
  overflow: hidden;
  background: -webkit-linear-gradient(top left, #3b945e, #3b945e 25%, #57ba98 25%, #57ba98 80%, #3b945e 80%, #3b945e);
  background: linear-gradient(to bottom right, #3b945e, #3b945e 25%, #57ba98 25%, #57ba98 80%, #3b945e 80%, #3b945e);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.social-media-icon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-transition: all ease 0.2s;
  transition: all ease 0.2s; }

footer {
  background-color: #182628;
  padding: 20px; }

